body {
  background: #f6f8fa;
  font-family: 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
  color: #222;
}

.App {
  max-width: 1100px;
  margin: 40px auto 0 auto;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  padding: 32px 32px 48px 32px;
}

.section {
  margin-bottom: 32px;
  text-align: left;
}

.section h2 {
  font-size: 1.3rem;
  color: #2d3a4b;
  margin-bottom: 16px;
  font-weight: 600;
}

input[type="text"], input[type="date"], select {
  padding: 7px 12px;
  border: 1px solid #d0d7de;
  border-radius: 6px;
  font-size: 1rem;
  margin-right: 8px;
  margin-bottom: 6px;
  background: #f8fafc;
  transition: border 0.2s;
}
input[type="text"]:focus, input[type="date"]:focus, select:focus {
  border: 1.5px solid #4f8cff;
  outline: none;
}

button {
  padding: 7px 18px;
  background: linear-gradient(90deg, #4f8cff 0%, #6fc3ff 100%);
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  margin-right: 8px;
  box-shadow: 0 2px 8px rgba(79,140,255,0.08);
  transition: background 0.2s, box-shadow 0.2s;
}
button:hover {
  background: linear-gradient(90deg, #357ae8 0%, #4f8cff 100%);
  box-shadow: 0 4px 16px rgba(79,140,255,0.13);
}

/* 表格样式 */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 8px;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
}
th, td {
  border: 1px solid #e5e9f2;
  padding: 10px 8px;
  text-align: left;
  font-size: 1rem;
}
th {
  background: #f3f6fa;
  color: #3a4a5d;
  font-weight: 600;
}
tr:nth-child(even) td {
  background: #f8fafc;
}
tr:hover td {
  background: #eaf3ff;
}

.pagination button {
  margin: 0 4px;
  padding: 6px 14px;
  font-size: 0.98rem;
}

.chart-container {
  width: 100%;
  height: 300px;
  background: #f8fafc;
  border-radius: 8px;
  margin: 12px 0 24px 0;
  box-shadow: 0 2px 8px rgba(79,140,255,0.06);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 800px) {
  .App {
    padding: 10px 2vw 30px 2vw;
  }
  .section {
    margin-bottom: 18px;
  }
  th, td {
    font-size: 0.95rem;
    padding: 7px 4px;
  }
}
